:root {
  --primary: dodgerblue;
  --primary-lighter: lightblue;
  --accent: white;
  --negative: #333;
}
.btn-close {
  border: none;
  font-size: 12px;
  padding: 6px 12px;
  border-radius: 4px;
  background-color: rgb(170, 0, 0);
  color: var(--accent);
  transition: all 0.2s;
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);

  &:hover {
    transform: translateY(-2px);
    background-color: var(--primary-lighter);
    color: var(--negative);
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
  }
}
.select-opt {
  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
  border: 1px solid black;
}
.btn {
  border: none;
  font-size: 12px;
  padding: 6px 12px;
  border-radius: 4px;
  background-color: var(--primary);
  color: var(--accent);
  transition: all 0.2s;

  &:hover {
    transform: translateY(-2px);
    background-color: var(--primary-lighter);
    color: var(--negative);
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.2);
  }
}
button {
  border: none;
  font-size: 12px;
  padding: 6px 12px;
  border-radius: 4px;
  background-color: var(--primary);
  color: var(--accent);
  transition: all 0.2s;

  &:hover {
    transform: translateY(-2px);
    background-color: var(--primary-lighter);
    color: var(--negative);
  }
}
